export default {
  middleware: "authenticated",
  data() {
    return {
      form: {
        identifiant: "",
        nom: "",
        prenom: "",
        email: "",
        telephone: "",
      },
      show: true,
    };
  },
  methods: {
    onSubmit(evt) {
      evt.preventDefault();
      alert(JSON.stringify(this.form));
    },
    onClick(evt) {
      this.$store.commit("logout");
      this.$router.push("/");
    },
  },
};
